import React from "react";
//import Navitem from "./Navitem";
import {
  Link
} from "react-router-dom";
import { slide as Menu } from "react-burger-menu";
import henrysus10pdf from '../assets/HSusantin_Resume.pdf'

//Fix the resume download button it doesn't work

export default (props) => {
  return (
    <Menu {...props}>
      <Link to="/">Home</Link>
      <Link to="/about">About</Link>
      <Link to="/education">Education</Link>
      <Link to="/skills">Skills</Link>
      <a href={henrysus10pdf} download="HSusantin_Resume.pdf" target="_blank" rel="noopener noreferrer" download>Resume</a>
    </Menu>
  );
};